.control-container {
  /* box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box; 
  border: 3px solid #000;*/
  /* border-radius: 15px; */
  height: 7%;
  display: flex;
  justify-content: space-around;
}

.control-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 95%;
  padding: 1rem;
  font-size: 1.5rem;
  background: #fff;
  box-shadow: rgb(0 0 0 / 15%) 0px -0.2rem 0.2rem;
  z-index: 1;
}

.control-box-hide {
  display: none;
}

.timing-box-timing, .timing-box-stopped, .score-box, .timing-box-reset {
  display: flex;
}

.timing-box-paused {
  display: flex;
  animation: blinker 1.2s linear infinite;
}

@keyframes blinker {
  50% {
    opacity: 0;
  }
}

.timer-box {
  margin: auto;
  margin-left: .3rem;
}

.percent-box {
  margin: auto;
  margin-left: .2rem;
}

.timer-img {
  border: .2rem solid #000;
  border-radius: .7rem;
  max-height: 1.5rem;
  margin: auto;
  /* -webkit-filter: invert(100%); 
  filter: invert(100%); */
}

.score-img {
  max-height: 2rem;
  margin: auto;
  /* -webkit-filter: invert(100%); 
  filter: invert(100%); */
}

@media only screen and (min-width: 450px) {
  .control-box {
    padding: 1rem 2rem;
  }
}

@media only screen and (min-width: 600px) {
  .control-box {
    padding: 1rem 3.5rem;
  }
}

/** Tablet **/
@media only screen and (min-width: 800px) {
  .control-box, .control-box-hide {
    padding: .5rem .5rem;
    margin-bottom: .5rem;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    border-radius: 1rem;
    box-shadow: rgb(0 0 0 / 15%) 0px -0.3rem 0.2rem;
    font-size: 1.3rem;
  }

  .control-box-hide {
    opacity: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 95%;
  }

  .control-container {
    height: unset;
    background: rgba(0, 0, 0, .2);
    border-bottom-left-radius: 1rem;
    border-bottom-right-radius: 1rem;
  }
}

@media only screen and (min-width: 900px) {
  .control-box {
    font-size: 1.5rem;
    padding: .5rem 1rem;
  }
}