.multiple-choice-container {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
}

.multiple-choice-option {
  font-weight: bold;
  font-size: 1.1rem;
  background: #b3b3b3;
  border: .3rem solid #b3b3b3;
  color: #fff;
  border-radius: 1rem;
  padding: .25rem .45rem;
  margin-right: .5rem;
  margin-top: .3rem;
}

button:focus {
  outline:0;
}

.multiple-choice-option:hover {
  cursor: pointer;
  background: #000000;
  border: .3rem solid #000000;
}

.multiple-choice-option:disabled {
  display: none;
}