.theme-green-chatbot {
  background-color: #43e629;
}

.theme-blue-chatbot {
  background-color: #1ecbe1;
}

.theme-orange-chatbot {
  background-color: #fbb408;
}

.theme-dark-blue-chatbot {
  background-color: #07498b;
}

.theme-dark-green-chatbot {
  background-color: #1d5818;
}

.theme-dark-red-chatbot {
  background-color: #6d0000;
}

.sara-chan {
  position: fixed;
  height: 100%;
  width: 100vw;
  background-attachment: fixed;
  background-size: cover;
}

.chat-bg-1 {
  background-image: url("https://the-real-han.github.io/sara-chan-bot/img/bg_1.png");
}

.chat-bg-2 {
  background-image: url("https://the-real-han.github.io/sara-chan-bot/img/bg_2.png");
}

.chat-bg-3 {
  background-image: url("https://the-real-han.github.io/sara-chan-bot/img/bg_3.png");
}

.chat-bg-4 {
  background-image: url("https://the-real-han.github.io/sara-chan-bot/img/bg_4.png");
}

.chat-bg-5 {
  background-image: url("https://the-real-han.github.io/sara-chan-bot/img/bg_5.png");
}

.chat-bg-6 {
  background-image: url("https://the-real-han.github.io/sara-chan-bot/img/bg_6.png");
}

.chat-bg-0 {
  background-image: url("https://the-real-han.github.io/sara-chan-bot/img/bg_0.png");
}

.react-chatbot-kit-chat-inner-container {
  height: 100%;
  background-color: transparent;
  border-radius: .7rem;
}

.react-chatbot-kit-chat-message-container {
  padding: 1rem 1.5rem 0rem 1.5rem;
  overflow: auto;
  height: calc(100% - 1rem);
  background: transparent;
}

.react-chatbot-kit-user-chat-message-container {
  display: flex;
  margin: .3rem 0;
  justify-content: flex-end;
}

.react-chatbot-kit-user-avatar-container {
  display: none;
}

.react-chatbot-kit-chat-bot-message-container {
  display: flex;
  margin: .3rem 0;
  justify-content: flex-start;
}

.react-chatbot-kit-chat-bot-avatar-container {
  display: none;
}

.react-chatbot-kit-user-chat-message {
  /* background-color: #1982FC; */
  background: rgba(0,0,0,.4);
  color: #fff;
  padding: .7rem;
  border-radius: .7rem;
  font-size: 1.1rem;
  font-weight: medium;
  position: relative;
  text-align: left;
}

.react-chatbot-kit-user-chat-message-arrow {
  width: 0;
  height: 0;
  border-top: .6rem solid transparent;
  border-bottom: .6rem solid transparent;
  border-left: .6rem solid rgba(0,0,0,.4);
  position: absolute;
  right: -.6rem;
  top: .9rem;
  display: none;
}

.react-chatbot-kit-chat-bot-message {
  /* background-color: #5BC236 !important; */
  color: #000; 
  background-color: #fff !important; 
  padding: .7rem;
  border-radius: .7rem;
  font-size: 1.1rem;
  font-weight: medium;
  position: relative;
  text-align: left;
}

.react-chatbot-kit-chat-bot-message-arrow {
  width: 0;
  height: 0;
  border-top: .6rem solid transparent;
  border-bottom: .6rem solid transparent;
  border-right: .6rem solid #fff !important;
  position: absolute;
  left: -.6rem;
  top: .9rem;
  display: none;
}

.react-chatbot-kit-chat-bot-loading-icon-container {
  height: 1.3rem;
  width: 1.8rem;
}

.chatbot-loader-container {
  display: flex;
  width: 100%;
  justify-content: center;
}

#chatbot-loader #chatbot-loader-dot1 {
  animation: load .5s infinite;
}

#chatbot-loader #chatbot-loader-dot2 {
  animation: load .5s infinite;
  animation-delay: 0.1s;
}

#chatbot-loader #chatbot-loader-dot3 {
  animation: load .5s infinite;
  animation-delay: 0.2s;
}

@keyframes load {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

#chatbot-loader {
  fill: #000;
}

.react-chatbot-kit-chat-container {
  position: relative;
  height: 85%;
  background: transparent;
}

.react-chatbot-kit-chat-header {
  display: none;
}

.react-chatbot-kit-chat-input-container {
  display: none;
}

.chatbot-header {
  display: none;
}

/** Tablet **/
@media only screen and (min-width: 850px) {

  .theme-green-chatbot, .theme-blue-chatbot, .theme-orange-chatbot, 
  .theme-dark-blue-chatbot, .theme-dark-red-chatbot, .theme-dark-green-chatbot {
    background-color: transparent;
  }

  .sara-chan {
    position: unset;
    background-image: none;
    padding: 0rem 3vw 1rem 1rem;
    color: #000;
  }

  .chatbot-header {
    display: flex;
    justify-content: space-between;
    background: #fff;
    position: relative;
    z-index: 1;
    box-shadow: rgb(0 0 0 / 15%) 0px 0.3rem 0.2rem;
    border-top-left-radius: 1rem;
    border-top-right-radius: 1rem;
  }

  .chatbot-name, .chatbot-reload-button {
    display: flex;
    padding: .3rem 1.3rem;
    font-family: 'Texta', sans-serif;
    font-weight: bold;
    font-size: 1.2rem;
    cursor: pointer;
  }

  .reload-img {
    border: .2rem solid #000;
    border-radius: .7rem;
    max-height: 1.4rem;
    margin: auto;
    padding: .3rem;
  }

  .react-chatbot-kit-chat-container {
    background: rgba(0,0,0,.2);
  }
}

/** Desktop **/
@media only screen and (min-width: 1000px) {
  .sara-chan {
    padding: 0rem 7vw 1rem 2rem;
  }
}

@media only screen and (min-width: 1200px) {
  .sara-chan {
    padding: 0rem 10vw 1rem 3rem;
  }
}