/*! Flickity v2.1.2
https://flickity.metafizzy.co
---------------------------------------------- */

.flickity-enabled {
    position: relative;
  }
  
  .flickity-enabled:focus {
    outline: none;
  }
  
  .flickity-viewport {
    overflow: hidden;
    position: relative;
    height: 90%;
    width: 100%;
  }
  
  .flickity-slider {
    position: absolute;
    width: 100%;
    height: 100%;
  }
  
  /* draggable */
  
  .flickity-enabled.is-draggable {
    -webkit-tap-highlight-color: transparent;
    tap-highlight-color: transparent;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }
  
  .flickity-enabled.is-draggable .flickity-viewport {
    cursor: move;
    cursor: -webkit-grab;
    cursor: grab;
  }
  
  .flickity-enabled.is-draggable .flickity-viewport.is-pointer-down {
    cursor: -webkit-grabbing;
    cursor: grabbing;
  }
  
  /* ---- flickity-button ---- */
  
  .flickity-button {
    /* position: absolute;
    background: hsla(0, 0%, 100%, 0.75);
    border: none;
    color: #333; */
    display: none;
  }
  
  .flickity-button:hover {
    background: white;
    cursor: pointer;
  }
  
  .flickity-button:focus {
    outline: none;
    box-shadow: 0 0 0 5px #19f;
  }
  
  .flickity-button:active {
    opacity: 0.6;
  }
  
  .flickity-button:disabled {
    opacity: 0.3;
    cursor: auto;
    /* prevent disabled button from capturing pointer up event. #716 */
    pointer-events: none;
  }
  
  .flickity-button-icon {
    fill: #333;
  }
  
  /* ---- previous/next buttons ---- */
  
  .flickity-prev-next-button {
    top: 50%;
    width: 44px;
    height: 44px;
    border-radius: 50%;
    /* vertically center */
    transform: translateY(-50%);
  }
  
  .flickity-prev-next-button.previous {
    left: 10px;
  }
  .flickity-prev-next-button.next {
    right: 10px;
  }
  /* right to left */
  .flickity-rtl .flickity-prev-next-button.previous {
    left: auto;
    right: 10px;
  }
  .flickity-rtl .flickity-prev-next-button.next {
    right: auto;
    left: 10px;
  }
  
  .flickity-prev-next-button .flickity-button-icon {
    position: absolute;
    left: 20%;
    top: 20%;
    width: 60%;
    height: 60%;
  }
  
  /* ---- page dots ---- */
  
  .flickity-page-dots {
    position: absolute;
    width: 100%;
    bottom: -.7rem;
    padding: 0;
    margin: 0;
    list-style: none;
    text-align: center;
    line-height: 1;
  }
  
  .flickity-rtl .flickity-page-dots {
    direction: rtl;
  }
  
  .flickity-page-dots .dot {
    display: inline-block;
    width: 15px;
    height: 5px;
    margin: 0 8px;
    background: #333;
    border-radius: 5px;
    opacity: 0.25;
    cursor: pointer;
  }
  
  .flickity-page-dots .dot.is-selected {
    opacity: 1;
  }

  .section-box-1, .section-box-2, .section-box-3 {
    border-radius: .5rem;
    width: 85%;
    margin: 0rem 4rem 1rem 4rem;
  }
  
  .content-details {
    padding: .7rem 1.5rem;
    line-height: 1.4rem;
    margin: 0rem;
    /* text-align: justify;
    text-justify: inter-word; */
    text-align: center;
    color: #000;
}

.section-title {
  padding: .7rem 1.5rem;
  /* color: hsla(0,0%,100%,.65); */
  color: #fff;
  font-size: 1.4rem;
  font-family: 'Texta', sans-serif;
  background: rgba(0,0,0,.3);
  border-radius: 3rem;
  width: 50%;
  margin: auto;
}

.link-box {
  font-size: .9rem;
  font-weight: bold;
  border-radius: 1rem;
  padding: .4rem .8rem;
  margin: .3rem .5rem;
  display: inline-block;
}

a:link, input.s3, a.s2 {
  color: #fff;
  text-decoration: none;
  border: none;
  cursor: pointer;
}

input.s3:focus {
  outline: none;
}

a:visited {
  color: #fff;
}

.section-content {
  margin-top: .7rem;
  background: #fff;
  border-radius: 1rem;
  box-shadow: 0 .5rem .5rem rgb(57 76 96 / 15%);
  font-size: 1.1rem;
}
.action-box {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding-bottom: 1rem;
}

/** Tablet **/
@media only screen and (min-width: 500px) {
  .section-content {
    margin-top: 1rem;
  }

  .section-box-1, .section-box-2, .section-box-3 {
    width: 65%;
    margin: 0rem 6rem 1rem 6rem;
  }

  .link-box {
    font-size: 1.1rem;
    padding: .5rem 1rem;
  }
}

@media only screen and (min-width: 800px) {
  .section-box-1, .section-box-2, .section-box-3 {
    width: 85%;
  }
}
