.theme-green {
  background-color: #43e629 !important;
}

.theme-blue {
  background-color: #1ecbe1 !important;
}

.theme-orange {
  background-color: #fbb408 !important;
}

.theme-dark-blue {
  background-color: #07498b !important;
}

.theme-dark-green {
  background-color: #1d5818 !important;
}

.theme-dark-red {
  background-color: #6d0000 !important;
}

.main {
  align-items: center;
  /* background: linear-gradient(180deg, #040d3d 0%, #1ecbe1 100%); */
  /* background: linear-gradient(-90deg,#e23b79,#582faa);*/
  position: fixed;
  height: 100%;
  font-family: "Soleil_Medium",Helvetica,Arial,sans-serif;
  font-size: 1.2rem;
}

.home {
  position: fixed;
  height: 92%;
  text-align: center;
  width: 100vw;
  background-image: url('https://the-real-han.github.io/sara-chan-bot/img/wave_bg.png');
  background-repeat: no-repeat;
  background-position: center;
}

.home-content {
  position: relative;
  width: 100%;
  height: 100%;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

.chat {
  position: fixed;
  height: 92%;
}

.home-hide, .chat-hide {
  display: none;
}

.menu-bar {
  display: flex;
  display: -ms-flexbox;
  -ms-flex-pack: space-between;
  justify-content: space-between;
  display: -webkit-flex;
  -webkit-justify-content: space-between;
  -webkit-align-items: space-between;
  align-items: center;
  height: 8%;
  width: 100vw;
  background: #fff;
  font-family: 'Texta', sans-serif;
  position: relative;
  z-index: 1;
  box-shadow: rgb(0 0 0 / 15%) 0px 0.2rem 0.2rem;
}

.name {
  margin-left: 1rem;
  font-weight: bolder;
  font-size: 1.3rem;
  display: flex;
  color: #000;
}

.name-text {
  margin: auto;
  padding: 0rem .5rem;
}

.nav {
  display: flex;
}

.nav-button, .nav-button-active {
  display: block;
  margin-right: 1rem;
  padding: .2rem 0rem;
  font-weight: bold;
  font-size: 1.1rem;

}

.nav-button {
  color: #9c9c9c;
}

.nav-button-active {
  color: #000;
}

.title {
  font-size: 1.7rem;
  padding: 3.5% 0px 0px 0px;
  margin: 0px;
  font-weight: 700;
  color: #fff;
}

.description {
  margin: 1.5% 2rem 2% 2rem;
  font-size: 1.2rem;
  line-height: 1.5rem;
  color: #fff;
}

.icon-img {
  max-height: 2.5rem;
  margin: auto;
  display:inline-block;
  /* -webkit-mask:url("../public/img/icon.png") center/contain no-repeat;
        mask:url("../public/img/icon.png") center/contain no-repeat; */
  /* -webkit-filter: invert(100%); 
  filter: invert(100%); */
  /* filter: invert(39%) sepia(77%) saturate(1443%) hue-rotate(111deg) brightness(91%) contrast(104%); */
}

.arrow-box {
  text-align: right;
  padding: 3% 3rem 0px 0px;
}

.arrow-img {
  max-height: 3.5rem;
  -webkit-filter: invert(100%); 
  filter: invert(100%);
  /* transform: scaleX(-1); */
}

.rotate-phone {
  height: 5rem;
  width: 10rem;
  border: .5rem solid white;
  border-radius: 1rem;
  animation: rotate 1.5s ease-in-out infinite alternate;
}

.rotate-message {
  color: white;
  font-size: 1.5rem;
  font-weight: bold;
  padding: 10% 5rem 0rem 5rem;
  text-align: center;
}

.rotate-device {
  display: none;
}

@keyframes rotate {
  0% {
		transform: rotate(0deg)
	}
	50% {
		transform: rotate(-90deg)
	}
	100% {
		transform: rotate(-90deg)
	}
}

/** Tablet **/
@media only screen and (min-width: 850px) {
  .main {
    align-items: center;
    background-image: url('https://the-real-han.github.io/sara-chan-bot/img/wave_bg.png');
    background-repeat: no-repeat;
    background-position: left;
    color: #fff;
    min-height: 100%;
    width: 100%;
    display: -ms-flexbox;
    -ms-flex-pack: distribute;
    display: flex;
    justify-content: space-around;
    display: -webkit-flex;
    -webkit-justify-content: space-around;
    -webkit-align-items: space-around;
    font-size: 1.2rem;
  }

  .arrow-box {
    padding: 10% 3rem 0px 0px;
  }

  .menu-bar {
    display: none;
  }

  .home {
    background-color: transparent;
    background-image: none;
    position: relative;
    height: 95%;
    width: 50%;
    padding: 0rem 0rem 0rem 2vw;
  }

  .chat-hide {
    position: initial;
    height: 80%;
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .arrow-box {
    padding-right: 0rem;
  }

  .arrow-img {
    transform: rotate(45deg);
  }
}

/** Desktop **/
@media only screen and (min-width: 1000px) {
  .home {
    padding: 0rem 2rem 0rem 7vw;
  }
}

/** Desktop **/
@media only screen and (min-width: 1200px) {
  .title {
    font-size: 2rem;
  }

  .description {
    font-size: 1.3rem;
  }

  .home {
    padding: 0rem 3rem 0rem 10vw;
  }
}

@media only screen and (max-width: 850px) and (orientation: landscape),
only screen and (min-width: 650px) and (orientation: portrait) {
  .main {
    display: none;
  }

  .rotate-device {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: #000;
    height: 100vh;
    width: 100vw;
  }
}